import React, {Suspense} from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import Settings from "./components/Content/Settings/Settings";
import {HashRouter, Route, withRouter} from "react-router-dom";
import {BrowserRouter} from "react-router-dom";
import News from "./components/Content/News/News";
import Music from "./components/Content/Music/Music";
// import DialogsContainer from "./components/Content/Dialogs/DialogsContainer";
import UsersContainer from "./components/Content/Users/UsersContainer";
// import ProfileContainer from "./components/Content/Profile/ProfileContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import LoginContainer from "./components/Content/Login/LoginContainer";
import {connect} from "react-redux";
import {setInitializedSuccess} from "./redux/reducers/app_reducer";
import Preloader from "./components/common/Preloader/Preloader";
import WithSuspenseComponent from "./redux/hoc/withSuspenseComponent";

const DialogsContainer = React.lazy(() => import("./components/Content/Dialogs/DialogsContainer"));
const ProfileContainer = React.lazy(() => import("./components/Content/Profile/ProfileContainer"));

class App extends React.Component {
    componentDidMount() {
        this.props.setInitializedSuccess();
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }
        return (
          <HashRouter basename={process.env.PUBLIC_URL}>
              <div className='app-wrapper'>
                  <HeaderContainer/>
                  <Navbar/>
                  <div className="app-wrapper-content">

                      {/*First option*/}
                      {/*<Route path="/dialogs">*/}
                      {/*    <Dialogs dialogs={props.dialogs} messages={props.messages}/>*/}
                      {/*</Route>*/}
                      {/*<Route path='/profile'>*/}
                      {/*    <Profile posts={props.posts}/>*/}
                      {/*</Route>*/}

                      {/*Second option*/}
                      <Route path="/dialogs" render={WithSuspenseComponent(DialogsContainer)}/>
                      <Route path="/profile/:userId?" render={WithSuspenseComponent(ProfileContainer)}/>
                      <Route path='/settings' component={Settings}/>
                      <Route path='/news' component={News}/>
                      <Route path='/music' component={Music}/>
                      <Route path='/users' render={() => <UsersContainer/>}/>
                      <Route path='/login' render={() => <LoginContainer/>}/>
                  </div>
              </div>
          </HashRouter>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        initialized: state.app.initialized,
        isAuth: state.auth.isAuth,
    }
}
export default connect(mapStateToProps, {setInitializedSuccess})(App);