import React from 'react'
import {Field, reduxForm} from "redux-form";
import {Input} from "../../common/FieldComponents/FieldComponents";
import {maxLength, required} from "../../../redux/utilities/validators";
import s from '../../common/FieldComponents/FieldComponents.module.css'

const maxlength40 = maxLength(40);
export const LoginForm = (props) => {
    return (
      <form onSubmit={props.handleSubmit}>
          <div>
              <Field name={'email'} type={'email'} component={Input} validate={[required, maxlength40]}
                     placeholder='login'/>
          </div>
          <div>
              <Field name={'password'} component={Input} validate={[required, maxlength40]} type='password'
                     placeholder='password'/>
          </div>
          <div>
              <Field name={'rememberMe'} component={'input'} type='checkbox'/>Remember me
          </div>
          { props.error && <div className={s.formSummaryError}>
              {props.error}
          </div>}


          <div>

              <button type={'submit'}>Login</button>
          </div>

      </form>
    )
}
export const ReduxLoginForm = reduxForm({form: 'login'})(LoginForm)

export default ReduxLoginForm;
