import React from 'react';
import s from "./Friend.module.css";

const Friend = (props) => {
    return (
      <div className={s.friend}>
          <div className={s.friendName}>
              {props.name}
          </div>
          <div className={s.friendAvatar}>
              <img src={props.avatar}/>
          </div>
      </div>
    )
}

export default Friend;