import React from "react";
import {connect} from "react-redux";
import {followThunkCreator, getUsersThunkCreator, unfollowThunkCreator,} from "../../../redux/reducers/users_reducer";
import Users from "./Users";
import Preloader from "../../common/Preloader/Preloader";
import {compose} from "redux";
import {
    getCurrentPage,
    getFollowingProgress,
    getIsLoading,
    getPageSize,
    getTotalUsersCount,
    getUsers,
    isAuth
} from "../../../redux/reducers/users_selectors";

class UsersContainer extends React.Component {
    componentDidMount() {
       this.props.getUsersThunkCreator(this.props.currentPage, this.props.pageSize);
    }

    onPageChanged = (currentPage) => {
        this.props.getUsersThunkCreator(currentPage, this.props.pageSize);
    }

    render() {
        return (<>
            {this.props.isLoading ? <Preloader/> : null}
            <Users onPageChanged={this.onPageChanged}
                   toggleFollowingProgress={this.props.toggleFollowingProgress}
                   currentPage={this.props.currentPage}
                   totalUsersCount={this.props.totalUsersCount}
                   pageSize={this.props.pageSize}
                   users={this.props.users}
                   isLoading={this.props.isLoading}
                   followingProgress={this.props.followingProgress}
                   follow = {this.props.followThunkCreator}
                   unfollow={this.props.unfollowThunkCreator}
                   isAuth={this.props.isAuth}

            />
        </>)
    }

}

let mapStateToProps = (state) => {

    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isLoading: getIsLoading(state),
        followingProgress : getFollowingProgress(state),
        isAuth: isAuth(state),
    }
}

export default compose(
  connect(mapStateToProps, {getUsersThunkCreator, followThunkCreator, unfollowThunkCreator}))(UsersContainer);
