import React from 'react';
import s from "./Navbar.module.css";
import NavLinks from "./NavLinks/NavLinks";
import FriendsContainer from "./Friends/FriendsContainer";

console.log(s);

const Navbar = () => {
    return (
      <div className={s.nav}>
          <NavLinks/>

          <div className={s.friends}>
              <div>Friends</div>
              <div>
                  <FriendsContainer/>
              </div>
          </div>
      </div>
    )
}

export default Navbar;