const SEND_MESSAGE = 'dialogs/SEND-MESSAGE';
let initialState = {
    dialogs: [
        {id: 1, name: 'Vlad',},
        {id: 2, name: 'Matvei',},
        {id: 3, name: 'Larisa',},
        {id: 4, name: 'Ulya',},
        {id: 5, name: 'Vitalya',},
        {id: 6, name: 'Natasha',},
    ],
    messages: [
        {
            id: 1,
            message: "Hello!",
            from: 1,
            to: 0,
            avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
        },
        {
            id: 0,
            message: "Hello!",
            from: 0,
            to: 1,
            avatar: "https://upload.wikimedia.org/wikipedia/commons/a/a0/Pierre-Person.jpg",
        },
        {
            id: 1,
            message: "How are you?",
            from: 1,
            to: 0,
            avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
        },
        {
            id: 0,
            message: "I'm fine! Thank you!!",
            from: 0,
            to: 1,
            avatar: "https://upload.wikimedia.org/wikipedia/commons/a/a0/Pierre-Person.jpg",
        },
        {
            id: 1,
            message: "Good bye!",
            from: 1,
            to: 0,
            avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
        },
        {
            id: 0,
            message: "Bye!!!",
            from: 0,
            to: 1,
            avatar: "https://upload.wikimedia.org/wikipedia/commons/a/a0/Pierre-Person.jpg",
        },
    ],
}
const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let message = {
                id: 1,
                message: action.textMessage,
                from: 0,
                to: 1,
                avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
            }
            if (message.message === undefined) {
                return state;
            }
            return {
                ...state,
                messages: [...state.messages, message],
            }
        default:
            return {...state};


    }
}


export const sendMessage = (textMessage) => {
    return {
        type: SEND_MESSAGE,
        textMessage
    }
}

export default dialogsReducer;