import React from 'react';
import Header from "./Header";
import {connect} from "react-redux";
import {getAuthUserThunkCreator, logoutAuthUserThunkCreator} from "../../redux/reducers/auth_reducer";


class HeaderContainer extends React.Component {

    render() {
        return (
         <Header {...this.props}/>
        )
    }
}
export const mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth,
        login: state.auth.login,
    }
}

export default connect(mapStateToProps,{logoutAuthUserThunkCreator} )(HeaderContainer);