import {profileAPI} from "../../api/api";

const ADD_POST = 'profile/ADD-POST';
const SET_USER_PROFILE = 'profile/SET-USER-PROFILE';
const OPEN_URL = 'profile/OPEN-URL';
const SET_STATUS = 'profile/SET-STATUS';
const UPDATE_STATUS = 'profile/UPDATE-STATUS';
const DELETE_POST = 'profile/DELETE-POST'

let initialState = {
    posts: [
        {id: 1, message: "Hello!", likesCount: 12},
        {id: 2, message: "Hello! AWESOME!", likesCount: 14},
        {id: 3, message: "Hello! WONDERFULL!", likesCount: 15},
        {id: 4, message: "Hello! BEAUTIFULL!", likesCount: 12},
        {id: 5, message: "Hello! COOL!", likesCount: 111},
        {id: 6, message: "Hello! WELL!", likesCount: 152},
    ],
    profile: null,
    status: '',
}
const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: (state.posts.length + 1),
                message: action.postText,
                likesCount: 0,
            };
            if (newPost.message === undefined) {
                return state;
            }
            return {
                ...state,
                posts: [...state.posts, newPost],
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.postId),
            }

        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile,

            }
        case OPEN_URL:
            let url = action.url;
            if (url.includes('https://', 0) || url.includes('http://', 0)) {
                window.open(url, "_blank") //to open new page
            } else {
                url = 'http://' + url;
                window.open(url, "_blank") //to open new page
            }
            return {
                ...state,
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status,
            }
        case UPDATE_STATUS:
            return {
                ...state,
                status: action.status,
            }
        default:
            return state;
    }
}

export const addPost = (postText) => {
    return {
        type: ADD_POST,
        postText
    }
}
export const deletePost = (postId) => {
    return {
        type: DELETE_POST,
        postId
    }
}

export const setUserProfile = (profile) => {
    return {type: SET_USER_PROFILE, profile}
}

export const openUrl = (url) => {
    return {type: OPEN_URL, url}
}

export const setStatus = (status) => {
    return {type: SET_STATUS, status}
}
export const updateStatus = (status) => {
    return {type: UPDATE_STATUS, status}
}

export const getUserProfileThunkCreator = (userId) => async (dispatch) => {
    let response = await profileAPI.getProfile(userId)
    dispatch(setUserProfile(response.data))
}
export const getStatusProfileThunkCreator = (userId) => async (dispatch) => {
    let response = await profileAPI.getStatus(userId)
    dispatch(setStatus(response.data))
}

export const updateStatusProfileThunkCreator = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(updateStatus(status))
    }
}

export default profileReducer;