import {usersAPI} from "../../api/api";

const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_USERS = 'users/SET-USERS';
const SET_CURRENT_PAGE = 'users/SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'users/SET-TOTAL-USER-COUNT';
const TOGGLE_IS_LOADING = 'users/TOGGLE-IS-LOADING';
const TOGGLE_FOLLOWING_PROGRESS = 'users/TOGGLE-FOLLOWING-PROGRESS';

let initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isLoading: true,
    followingProgress: [],
    fake: 10,
}
const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'FAKE':
            return {
                ...state,
                fake: state.fake + 1,
            }
        case FOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userID) {
                        return {...u, followed: true}
                    } else {
                        return u;
                    }
                }),
            }
        case UNFOLLOW:
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userID) {
                        return {...u, followed: false}
                    } else {
                        return u;
                    }
                }),
            }
        case SET_USERS:
            return {
                ...state,
                users: [...action.users],
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage,
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalUsersCount,
            }
        case TOGGLE_IS_LOADING:
            return {
                ...state,
                isLoading: action.isLoading,
            }
        case TOGGLE_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingProgress: action.isLoading ?
                  [...state.followingProgress, action.userId] :
                  state.followingProgress.filter(id => id != action.userId)
            }
        default:
            return state;
    }
}
export const follow = (userID) => {
    return {
        type: FOLLOW,
        userID,
    }
};
export const unfollow = (userID) => {
    return {
        type: UNFOLLOW,
        userID,
    }
};

export const setUsers = (users) => {
    return {
        type: SET_USERS,
        users,
    }
}

export const setCurrentPage = (currentPage) => {
    return {
        type: SET_CURRENT_PAGE,
        currentPage,
    }
}

export const setTotalUsersCount = (totalUsersCount) => {
    return {
        type: SET_TOTAL_USERS_COUNT,
        totalUsersCount,
    }
}

export const toggleIsLoading = (isLoading) => {
    return {
        type: TOGGLE_IS_LOADING,
        isLoading,
    }
}

export const toggleFollowingProgress = (isLoading, userId) => {
    return {
        type: TOGGLE_FOLLOWING_PROGRESS,
        isLoading,
        userId,
    }
}

export const getUsersThunkCreator = (currentPage, pageSize) => async (dispatch) => {
    dispatch(setCurrentPage(currentPage))
    dispatch(toggleIsLoading(true))
    let data = await usersAPI.getUsers(currentPage, pageSize)
    dispatch(toggleIsLoading(false))
    dispatch(setUsers(data.items))
    dispatch(setTotalUsersCount(data.totalCount));
}

export const unfollowThunkCreator = (userId) => async (dispatch) => {
    dispatch(toggleFollowingProgress(true, userId))
    let data = await usersAPI.unfollow(userId)
    if (data.resultCode == 0) {
        dispatch(unfollow(userId))
    }
    dispatch(toggleFollowingProgress(false, userId))
}

export const followThunkCreator = (userId) => async (dispatch) => {
    dispatch(toggleFollowingProgress(true, userId))
    let data = await usersAPI.follow(userId)
    if (data.resultCode == 0) {
        dispatch(follow(userId))
    }
    dispatch(toggleFollowingProgress(false, userId))
}


export default usersReducer;